.neon-menu-item {
    cursor: pointer;
    z-index: 2;
    text-align: center;
    width: 14vw;
    position: relative;
    display: inline-block;
    padding: 15px 10px;
    color: #49fb35;
    text-transform: uppercase;
    letter-spacing: .4vw;
    text-decoration: none;
    font-size: 2vw;
    overflow: hidden;
    transition: 0.2s;
}



.neon-menu-item:hover {
    color: white;
    box-shadow: 0 0 10px #49fb35, 0 0 40px #49fb35, 0 0 80px #49fb35;
    transition-delay: 1s;
}


.neon-menu-item span {
    position: absolute;
    display: block;
}




.neon-menu-item span:nth-child(1) {
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, transparent, #49fb35);
}



.neon-menu-item:hover span:nth-child(1) {
    left: 100%;
    transition: 1s;
}


.neon-menu-item span:nth-child(3) {
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg, transparent, #49fb35);
}

.neon-menu-item:hover span:nth-child(3) {
    right: 100%;
    transition: 1s;
    transition-delay: 0.5s;
}



.neon-menu-item span:nth-child(2) {
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(180deg, transparent, #49fb35);
}

.neon-menu-item:hover span:nth-child(2) {
    top: 100%;
    transition: 1s;
    transition-delay: 0.25s;
}



.neon-menu-item span:nth-child(4) {
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(360deg, transparent, #49fb35);
}

.neon-menu-item:hover span:nth-child(4) {
    bottom: 100%;
    transition: 1s;
    transition-delay: 0.75s;
}
