.shiny {
  color: white;
  border: none;
  background: transparent;
  border-radius: 10px;
  font-weight: bold;
  cursor: pointer;
  overflow: hidden;
}

.shiny::after {
  content: "";
  position: absolute;
  top: calc(0 * 1px - 50px);
  top: calc(var(--y, 0) * 1px - 50px);
  left: calc(0 * 1px - 50px);
  left: calc(var(--x, 0) * 1px - 50px);
  width: 100px;
  height: 100px;
  background: radial-gradient(#39FF14, #3984ff00 35%);
  /* background-image: url(https://i.makeagif.com/media/10-04-2014/ip13Cg.gif);
  border-radius: 50%; */


  opacity: 0;
  transition: opacity 0.2s;
}

.shiny:hover::after {
  opacity: 0.4;
}



