.scroll-down {
    z-index: 2;
    background: black;
    border: 2px solid #49fb35;
    border-radius: 20px;
    bottom: 30px;
    height: 50px;
    right: 30px;
    margin: 0 auto;
    position: fixed;
    width: 30px;
  }
  .scroll-down::before {
    animation: scrollDownAnimation 2s infinite;
    background-color: #49fb35;
    border-radius: 100%;
    content: '';
    height: 6px;
    left: 0;
    margin: 0 auto;
    position: absolute;
    right: 0;
    top: 10px;
    width: 6px;
  }
  @keyframes scrollDownAnimation {
    0% {
      opacity: 0;
      transform: translate(0, 0);
    }
    40% {
      opacity: 1;
    }
    80% {
      opacity: 0;
      transform: translate(0, 20px);
    }
    100% {
      opacity: 0;
    }
  }
